import { useState } from 'react'
import { Button } from 'react-bootstrap'
import Ospf from './Ospf'
import './Basic.css'
import { ToastContainer, toast } from 'react-toastify'
import 'react-toastify/dist/ReactToastify.css'

const Basic = ({ list, ID }) => {
  const [routers, setRouters] = useState(1)
  const [hostname, setHostname] = useState('RR')
  const [banner, setBanner] = useState('#Roberth Romero, SCM#')

  const getIPMask = () => {
    let rows = list.split('\n')
    let columns = rows.map((row) => row.split('\t'))
    return columns
  }

  const getInterfacesConfig = () => {
    toast.success('âš™ï¸ Interfaces Copiado!');
    const addresses = getIPMask()
    let data = ''

    addresses.map(
      (item) =>
        (data += `interface ${item[0]}\nip address ${item[1]} ${item[2]}\nno shutdown\n`)
    )
    navigator.clipboard.writeText(data)
  }

  const handleClick = () => {
    toast.success('âŒ¨ï¸  Copiado!')
    setRouters(routers + 1)

    let basicConfigs = `hostname ${hostname}_R${routers}\nbanner motd ~${banner}~\nusername cisco password cisco\nservice password-encryption\nno ip domain-lookup\nip domain name cisco.com\ncrypto key generate rsa\n1024\nline vty 0 4\ntransport input ssh\nlogin local\n`
    navigator.clipboard.writeText(basicConfigs)
  }

  const restartHandler = () => {
    setRouters(1)
    toast.info('Router ID Reseteado!');
  }

  return (
    <div className='basic'>
      <span className="router-id"> ðŸ¤– Router:  {ID}</span>
      <form>
        <label htmlFor='hostname'>Hostname</label>
        <input
          className='mr-3 ml-1'
          type='text'
          id='hostname'
          onChange={(e) => setHostname(e.target.value)}
        />
        <label htmlFor='banner'>Banner</label>
        <input
          className='mr-3 ml-1'
          type='text'
          id='banner'
          onChange={(e) => setBanner(e.target.value)}
        />
      </form>
      <Button variant='outline-primary ml-3 mr-5' onClick={handleClick}>
        Configuraciones Basicas
      </Button>
      <p className='mt-5' style={{ display: 'inline-block' }}>
        RouterID:{' '}
        <span style={{ color: 'green', fontWeight: 'bolder' }}>{routers}</span>
      </p>
      <Button variant='outline-secondary ml-3' onClick={restartHandler}>
        Reiniciar Router ID
      </Button>
      <br />
      <Button variant='outline-success ml-3 mt-3' onClick={getInterfacesConfig}>
        Configuracion Interfaces
      </Button>
      <Ospf getIPMask={getIPMask} />
      <ToastContainer
        position='top-right'
        theme='light'
        autoClose={2500}
        hideProgressBar={false}
        newestOnTop={false}
        closeOnClick
        rtl={false}
        pauseOnFocusLoss
        draggable
        pauseOnHover
      />{' '}
    </div>
  )
}

export default Basic
