import { useState } from 'react'
import { Button } from 'react-bootstrap'
import { toast } from 'react-toastify'
import './Ospf.css'

const Ospf = ({ getIPMask }) => {
  const [ospfID, setOspfID] = useState(115)

  const handleClick = () => {
    toast.success('ðŸ§­ OSPF Copiado');
    const addresses = getIPMask()
    let ospfText = `router ospf ${ospfID ? ospfID : '115'}\n`

    for (const address in addresses) {
      let ospf = getNetworkID(addresses[address][1], addresses[address][2])
      ospfText += `network ${ospf.address.join('.')} ${getWildcard(
        ospf.mask.join('.')
      )} area 0\n`
    }
    
    navigator.clipboard.writeText(ospfText)
  }

  const getNetworkID = (ip, mask) => {
    const ipArr = ip.split('.')
    const maskArr = mask.split('.')

    const ipMask = {
      address: ipArr,
      mask: maskArr,
    }

    const networkID = { ...ipMask }

    for (const value in ipMask.mask) {
      if (ipMask.mask[value] !== '255' && ipMask.mask[value] !== '0') {
        let size = getBlockSizeMask(ipMask.mask[value])
        let netID = Math.floor(ipMask.address[value] / size) * size
        networkID.address[value] = `${netID}`
      }
    }
    return networkID
  }

  const getBlockSizeMask = (mask) => {
    const blockSize = {
      128: 128,
      192: 64,
      224: 32,
      240: 16,
      248: 8,
      252: 4,
      255: 1,
    }
    return blockSize[mask]
  }

  const getWildcard = (mask) => {
    const maskArr = mask.split('.')
    for (const maskValue in maskArr) {
      if (maskArr[maskValue] !== '255') {
        let wildcard = getBlockSizeMask(maskArr[maskValue]) - 1
        maskArr[maskValue] = `${wildcard}`
      } else {
        maskArr[maskValue] = '0'
      }
    }
    return maskArr.join('.')
  }

  return (
    <div className='m-2' style={{ padding: '2%' }}>
      <label className='label' htmlFor='OSPF-ID'>
        Ospf ID
      </label>
      <input
        type='number'
        className="ml-3 mr-3 input-number"
        min="1"
        max="255"
        required
        placeholder={ospfID ? ospfID : 115}
        onChange={(e) => setOspfID(e.target.value)}
      />
      <Button className="btn-warning" onClick={handleClick}>
        Copiar OSPF
      </Button>
    </div>
  )
}

export default Ospf
